import { emitEffect } from './emit-effect.js';
export function navigateTo(thingOrUrl) {
    let url;
    if (typeof thingOrUrl === 'string') {
        // Validate URL
        url = new URL(thingOrUrl).toString();
    }
    else {
        url = new URL(thingOrUrl.permalink, 'https://www.reddit.com').toString();
    }
    void emitEffect({
        navigateToUrl: {
            url,
        },
        type: 5,
    });
}
